html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

a {
  text-decoration: none;
  cursor: pointer;
}

@font-face {
  font-family: 'Lato';
  font-style: normal;
  font-weight: 400;
  src: local("Lato Regular"), local("Lato-Regular"), url("../fonts/lato-regular.woff2") format("woff2"), url("../fonts/lato-regular.woff") format("woff");
}

@font-face {
  font-family: 'Lato';
  font-style: italic;
  font-weight: 400;
  src: local("Lato Italic"), local("Lato-Italic"), url("../fonts/lato-italic.woff2") format("woff2"), url("../fonts/lato-italic.woff") format("woff");
}

@font-face {
  font-family: 'Lato';
  font-style: normal;
  font-weight: 700;
  src: local("Lato Bold"), local("Lato-Bold"), url("../fonts/lato-bold.woff2") format("woff2"), url("../fonts/lato-bold.woff") format("woff");
}

@font-face {
  font-family: 'Lato';
  font-style: italic;
  font-weight: 700;
  src: local("Lato Bold Italic"), local("Lato-BoldItalic"), url("../fonts/lato-bolditalic.woff2") format("woff2"), url("../fonts/lato-bolditalic.woff") format("woff");
}

.icon-car {
  width: 1.13em;
  height: 1em;
  fill: #FFF;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-marker {
  width: 0.75em;
  height: 1em;
  fill: #5B6D7C;
}

.icon-route {
  width: 1em;
  height: 1em;
  fill: #5B6D7C;
}

.icon-target {
  width: 1em;
  height: 1em;
  fill: #5EABDD;
}

.icon-train {
  width: 0.89em;
  height: 1em;
  fill: #FFF;
}

.icon-walk {
  width: 0.5em;
  height: 1em;
  fill: #FFF;
}

* {
  font-family: 'Lato', Arial, sans-serif;
}

body {
  min-width: 1180px;
  color: #5b6d7c;
  background: #f3f7f8;
  font-family: 'Lato', Arial, sans-serif;
  font-size: 14px;
  line-height: 1.3;
}

.inner {
  position: relative;
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;
  max-width: 1180px;
}

.inner:after {
  content: " ";
  display: table;
  clear: both;
}

.title-md {
  font-size: 18px;
  font-weight: 700;
  line-height: 22px;
  text-transform: uppercase;
}

.label {
  font-size: 14px;
  font-weight: 700;
  line-height: 17px;
}

.btn {
  display: inline-block;
  vertical-align: top;
  padding: 11px 16px;
  border-radius: 4px;
  color: #fff;
  background: #51e2b8;
  box-shadow: 0 2px 19px 0 rgba(0, 0, 0, 0.1);
  font-size: 15px;
  font-weight: 700;
  line-height: 18px;
  text-transform: uppercase;
  transition: background 0.3s;
}

.btn:hover {
  background: #25dba6;
}

.btn:active {
  background: #21c696;
}

.btn_wide {
  width: 100%;
}

.input {
  display: inline-block;
  vertical-align: top;
  padding: 1px 0 3px;
  border-bottom: solid 1px #cfdee5;
  width: 100%;
  color: #5b6d7c;
  font-size: 14px;
  line-height: normal;
  transition: border-color 0.3s;
}

.input:focus {
  border-color: #5eabdd;
}

.input::placeholder {
  color: #cfdee5;
  opacity: 1;
}

.items__header {
  text-align: center;
}

.items__title {
  margin-bottom: 15px;
}

.items__description {
  margin-bottom: 10px;
}

.items__description p {
  font-size: 14px;
  line-height: 0.93;
}

.items__slider {
  margin: 0 -15px;
}

.items__slide {
  float: left;
  padding: 20px 15px;
  width: 33.33%;
}

.item {
  position: relative;
  padding: 20px 20px 20px 68px;
  border-radius: 4px;
  height: 200px;
  background: #fff;
  box-shadow: 0 7px 19px 2px rgba(0, 0, 0, 0.05);
}

.item__icon {
  position: absolute;
  top: 20px;
  left: 15px;
  font-size: 0;
}

.item__icon img {
  width: 39px;
  height: 39px;
}

.item__content {
  position: relative;
  padding-left: 21px;
  padding-bottom: 4px;
}

.item__content:before {
  content: '';
  position: absolute;
  top: -1px;
  left: 0;
  bottom: 0;
  border-left: solid 1px #50e3c2;
}

.item__title {
  margin-bottom: 10px;
  text-transform: uppercase;
}

.item__point {
  padding: 5px 0 6px;
  font-size: 14px;
  line-height: 15px;
}

.item__point:after {
  content: " ";
  display: table;
  clear: both;
}

.item__value {
  float: right;
  opacity: 0.5;
  font-size: 12px;
}

.item__label {
  overflow: hidden;
}

.map__inner {
  height: 500px;
  border-radius: 4px;
  box-shadow: 0 7px 19px 2px rgba(0, 0, 0, 0.05);
}

.map-controls {
  padding: 20px;
  border-radius: 4px;
  height: 500px;
  background: #fff;
  box-shadow: 0 7px 19px 2px rgba(0, 0, 0, 0.05);
}

.map-controls__header {
  margin-bottom: 20px;
  border: 1px solid rgba(207, 222, 229, 0.5);
  border-radius: 4px 4px 0 0;
  background: #fff;
  font-size: 0;
}

.map-controls__box {
  margin-bottom: 20px;
}

.map-controls__footer {
  padding: 20px 12px;
}

.map-type {
  position: relative;
  display: inline-block;
  vertical-align: top;
  padding: 5px 0;
  width: 50%;
  text-align: center;
  cursor: pointer;
}

.map-type + .map-type {
  border-left: 1px solid rgba(207, 222, 229, 0.5);
}

.map-type input {
  display: none;
}

.map-type input:checked ~ .map-type__title {
  font-weight: 700;
}

.map-type input:checked ~ .map-type__title:before {
  opacity: 1;
}

.map-type__icon, .map-type__title {
  display: block;
}

.map-type__icon {
  height: 31px;
  font-size: 0;
  text-align: center;
}

.map-type__icon .icon,
.map-type__icon img {
  display: inline-block;
  vertical-align: top;
  font-size: 31px;
}

.map-type__icon img {
  height: 100%;
}

.map-type__title {
  font-size: 14px;
  line-height: 17px;
}

.map-type__title:before {
  content: '';
  position: absolute;
  left: -1px;
  right: -1px;
  bottom: -1px;
  border-bottom: 2px solid #50e3c2;
  opacity: 0;
  transition: opacity 0.3s;
}

.map-directions__label, .map-directions__items {
  display: inline-block;
  vertical-align: middle;
}

.map-directions__label, .map-directions__item {
  margin-right: 12px;
}

.map-directions__item {
  display: inline-block;
  vertical-align: top;
  font-size: 0;
  cursor: pointer;
}

.map-directions__item input {
  display: none;
}

.map-directions__item input:checked + .map-directions__icon {
  background: #50e3c2;
}

.map-directions__icon {
  display: block;
  border-radius: 4px;
  width: 28px;
  height: 28px;
  color: #fff;
  background: #85d9dd;
  line-height: 28px;
  text-align: center;
  transition: background 0.3s;
}

.map-directions__icon .icon {
  display: inline-block;
  vertical-align: middle;
}

.map-directions__icon .icon-car {
  font-size: 16px;
}

.map-directions__icon .icon-train {
  font-size: 18px;
}

.map-directions__icon .icon-walk {
  font-size: 20px;
}

.map-addresses {
  position: relative;
  padding: 0 10px;
}

.map-addresses__route {
  position: absolute;
  left: 100%;
  bottom: 9px;
  margin-left: -7px;
  font-size: 16px;
}

.map-addresses__field {
  position: relative;
}

.map-addresses__field + .map-addresses__field {
  margin-top: 14px;
}

.map-addresses__icon {
  position: absolute;
  right: 100%;
  margin-right: 5px;
  width: 16px;
  height: 16px;
  font-size: 16px;
  text-align: center;
}

.map-addresses__icon-from {
  top: 1px;
}

.map-addresses__icon-to {
  bottom: 4px;
}

.map-addresses__dots {
  position: absolute;
  top: 17px;
  right: 100%;
  padding: 2px 0 1px;
  margin-right: 1px;
}

.map-addresses__dots:before, .map-addresses__dots:after,
.map-addresses__dots i {
  display: block;
  margin: 0 auto 2px;
  border-radius: 50%;
  width: 4px;
  height: 4px;
  opacity: 0.5;
  background: #5b6d7c;
}

.map-addresses__dots:before, .map-addresses__dots:after {
  content: '';
}

.location__header {
  padding: 32px 20px 30px;
  background: #fff;
  text-align: center;
}

.location__inner {
  padding-top: 40px;
  padding-bottom: 50px;
}

.location__container {
  margin: 0 -15px;
}

.location__container:after {
  content: " ";
  display: table;
  clear: both;
}

.location__map, .location__controls {
  float: right;
  padding: 0 15px;
}

.location__map {
  width: 75%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.location__controls {
  width: 25%;
}

.location__items {
  padding-top: 40px;
}
